const styles = {
  label: {
    position: 'relative',
    display: 'block',
    fontSize: '20px',
    marginBottom: '32px',
  },
  field: {
    boxSizing: 'border-box',
    display: 'block',
    width: '100%',
    padding: '10px 12px',
    border: 'none',
    borderBottom: '1px solid #2196f3',
    fontSize: '18px',
  },
  errorText: {
    position: 'absolute',
    fontSize: '12px',
    fontFamily: 'sans-serif',
    color: '#ff1744',
    bottom: '-2px',
    transform: 'translateY(100%)',
  },
}

export default styles;



