const styles = {
  card: {
    minHeight: '240px',
    display: 'flex',
    margin: '0 0 20px'
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
  },
  img: {
    width: '170px',
    flexShrink: '0'
  }
}

export default styles;



