export const errorSelector = (state) => (state.signUp.error);
export const nameSelector = (state) => (state.signUp.name);
export const surnameSelector = (state) => (state.signUp.surname);
export const emailSelector = (state) => (state.signUp.email);
export const passwordSelector = (state) => (state.signUp.password);



